@import url(https://fonts.googleapis.com/css?family=Open+Sans|Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

h1 {
    font-family: 'Open-Sans', sans-serif;
    font-size: 48px;
    margin: 10px 0;
    border-bottom: 2px solid #fff;
    display: inline-block;
}

.error {
    display: flex;
    align-items: center;
    justify-content: center;
}

.error-icon {
    width: 30px;
    height: 30px;
    margin-right: 20px;
}

.searchbar-container {
    height: 70px;
    width: 100%;
}

.header {
    color: #fff;
    box-sizing: border-box;
    width: 100%;
    height: 400px;
    background-image: linear-gradient(-180deg, rgba(50, 91, 132, 0.65) 0%, rgba(16, 45, 73, 0.65) 100%);
    padding: 50px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.header-container {
    width: 100%;
    background-size: cover;
    background-position: top center;
}

.todays-info {
    text-align: left;
}

.capitalized-info {
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
}

.todays-summary {
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    color: #EBDFDF;
}

.todays-forecast {
    box-sizing: border-box;
    padding: 20px;
    width: 420px;
    height: 320px;
    background-image: linear-gradient(-133deg, rgba(213,214,217,0.45) 0%, rgba(135,135,155,0.45) 100%);
    border-radius: 13px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
}

.current-details-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

span.current-temp {
    font-family: 'Roboto', sans-serif;
    font-size: 144px;
}

canvas.current-icon {
    width: 100px;
    height: 50px;
}

canvas.weekly-forecast-icon {
    width: 85%;
    max-width: 200px;
    height: auto;
}

.weekly-forecast-list {
    display: flex;
    justify-content: space-between;
}

.weekly-forecast-temps {
    text-align: center;
    font-weight: bold;
    font-size: 18px;
    font-family: 'Roboto', sans-serif;
}

.weekly-forecast-min-temp {
    color: #4E8AEA;
}

.weekly-forecast-max-temp {
    color: #F6732A;
}

.weekly-forecast-list {
    color: #ffffff;
    background: #263B50;
}

.weekly-forecast-item {
    text-align: center;
    width: 100%;
    border-right: 1px solid #000;
}

.weekly-forecast-item:first-child {
    border-left: 1px solid #000;
}


@media (max-width: 767px) {
  
    .weekly-forecast-list {
        flex-direction: column;
    }

    .weekly-forecast-item {
        border-bottom: 1px solid #000;
        border-right: none;
        border-left: none;
    }

}  


